document.body.style.setProperty(
    "--scrollbar-width",
    `${window.innerWidth - document.body.clientWidth}px`
);

// scripts of for all page
import 'lazysizes';
import tippy, {followCursor} from 'tippy.js';
require('../plugins/disableBodyScroll');
require('../plugins/popup/popup');

require('../users/input-password');

// custom scrtipts used for all pages
require('../../components/sidebar/sidebar');
require('../../components/mobile-menu/mobile-menu');

window.globalPopup = new Popup();

tippy('[data-tippy-content]', {
    allowHTML: true,
    delay: 0,
    appendTo: () => document.body,
    plugins: [followCursor],
});