// import SlimSelect from 'slim-select';
// –ù–µ –º–µ–Ω—è—Ç—å –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –∏–∑–º–µ–Ω–µ–Ω—ã–π –≤–∞—Ä–∏–∞–Ω—Ç –ø–ª–∞–≥–∏–Ω–∞ slim-select slimselect.es.js
import SlimSelect from '../../js/plugins/slim-select/dist/slimselect.es';

const elements = document.querySelectorAll('.js-select-box__select');

const detectIosMob = () => {
    const toMatch = [
        /iPhone/i,
        /iPad/i,
        /iPod/i,
    ];
    
    return toMatch.some((toMatchItem) => {
        return navigator.userAgent.match(toMatchItem);
    });
}

const isNeedShowSearch = (element) => {
    // –ü–æ –±—Ä–µ–∞–ø–æ–Ω–∏—Ç—É 1023 —Å—Ç—Ä–æ–∏—Ç—Å—è –º–æ–±–∏–ª—å–Ω—ã–π —Ñ–∏–ª—å—Ç—Ä –ø–æ—ç—Ç–æ–º—É –ø—Ä–æ–≤–µ—Ä—è–µ–º –ø–æ –Ω–µ–º—É —Ç–∞–∫ –∫–∞–∫ —Ç–∞–º –¥—Ä—É–≥–æ–µ –ø–æ–≤–µ–¥–µ–Ω–∏—è —Å–µ–ª–µ–∫—Ç–æ–≤ –ø–æ–¥—Ä–æ–±–Ω–æ—Å—Ç–∏ –Ω–∏–∂–µ
    const isPhone = window.matchMedia("(max-width: 1023px)").matches;
    const hasFilterClass = element.classList.contains('js-select-box-filter');

    if (hasFilterClass) {
        // –í –§–∏–ª—å—Ç—Ä–µ —Å–µ–ª–µ–∫—Ç —Å –ø–æ–∏—Å–∫–æ–º –±—É–¥–µ—Ç –Ω–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ —Ä–∞–±–æ–∞—Ç—å –≤ iphone –±–µ–∑ –ø–æ–¥–µ—Ä–∂–∫–∏ window.visualViewport –ø–æ—ç—Ç–æ–º—É –æ—Ç–∫–ª—é—á–∞–µ–º –ø–æ–∏—Å–∫
        // –ü–æ–¥—Ä–æ–±–Ω–æ—Å—Ç–∏ –≤ —Ñ–∞–π–ª–µ components/filte/filter.js –≤ 71 —Å—Ç—Ä–æ–∫–µ
        if (isPhone && detectIosMob && !window.visualViewport) {
            return false;
        } else {
            return true;
        }
    } else {
        return element.dataset.showSearch || false
    }
}

elements.forEach((element) => {
    console.log(element);
    

    const slimSelect = new SlimSelect({
        select: element,
        settings: {
            showSearch: isNeedShowSearch(element),
            searchPlaceholder: '–ü–æ–∏—Å–∫',
            searchText: '–ù–∏—á–µ–≥–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ',
            placeholderText: element.dataset.placeholderText,
            allowDeselect: element.dataset.allowDeselect,
            closeOnSelect: element.hasAttribute('multiple') === false,
        },
    });

    element.parentNode.classList.add('inited');
});